@import url('https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Inter&display=swap');

.about {
    margin: 0 30px;
    height: 100vh;
    display: grid;
    grid-template-areas: 'language' 'about' 'projects-skills' 'social-media';
    grid-template-rows: 0.6fr 0.3fr 0.2fr 0.4fr;
}

.language{
    display: flex;
}

.name {
    font-family: 'Balsamiq Sans', cursive;
    height: min-content;
    margin: 0;
}

.name h2 {
    font-size: 2.8vmax;
    margin: 0;
}

.name h1 {
    margin: 0;
}

.name p {
    max-width: 540px;
    font-size: 14px;
    font-family: 'Inter', sans-serif;
    color: #939090;
    margin: 0;
}

.projects-skills {
    width: 320px;
    display: flex;
    justify-content: space-between;
    padding: 0;
    height: min-content;
}

.my-projects {
    width: 103px;
    margin: 0;
    background-color: #FB8DE3;
    height: min-content;
    padding: 15px;
    border-radius: 100px;
    text-align: center;
    text-decoration: none;
}

.my-projects:hover {
    cursor: pointer;
}

.skills {
    margin: 0;
    height: min-content;
    width: 103px;
    padding: 15px;
    border: 2px solid #FB8DE3;
    border-radius: 100px;
    text-align: center;
    text-decoration: none;
}

.skills:hover {
    cursor: pointer;
}

.social-media img {
    width: 30px;
    margin: 0 8px;
}
