figure h2 {text-align: center;}
li {list-style: none;}

.my-skills {
    height: 100vh;
    display: grid;
}

.my-skills h1 {
    margin: 0;
    text-align: center;
}

.figure {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    transition: all 1s;
    opacity: 0;
}

.figure figure {
    width: 400px;
    height: 350px;
    background-color: #F5F5F5;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.figure ul li {
    display: grid;
    grid-template-columns: repeat(2, 2.7em);
    align-items: center;
}

.figure ul li img {
    width: 28px;
}

.figure ul li h4 {
    width: 200px;
}

/* Responsive Breakpoint */
@media screen and (max-width: 560px) {
    .figure {
        grid-template-columns: repeat(1, 1fr);
    }

    .my-skills {
        height: max-content;
    }
}